<template>
<div class="container mt-5">
    <div>
    <span class="products-background-text">Servicio</span>
    <span class="caracter-background-text">Ágil, Rápido y Confiable</span>
    </div>
    <b-row>
        <b-col cols="8">
           <div class="container py-5 mt-5" v-if="mostrar">
                <h1>Smart</h1>
                <br>
                <h3>Sistema automático</h3>
                <p class="px-5 mx-5">Solo coloca tu botella y esta se llenará automáticamente a la medida.
                </p>
                <b-button variant="outline-dark" size="lg" v-on:click="mostrar=!mostrar;substract()">
                    <i class="far fa-hand-pointer fa-2x"></i>
                </b-button>
            </div>

        <b-row v-else class="h-75 px-5 py-5 my-5">
            <b-col align-self="end" cols="3">
                <h4>Sirviendo</h4>
                <h1 class="monosp">600ml</h1>
            </b-col>
            <b-col align-self="center" class="w250" cols=6>
                <h4>Saldo</h4>
                <h1 class="display-1 monosp">{{ animatedNumber }}</h1>
            </b-col>
            <b-col align-self="end" cols="3">
                <h5>Procesando</h5>
                <h3>Agua fría</h3>
            </b-col>
        </b-row>         
        </b-col>

        <b-col cols="4" class="botella" sm="3">
            <div class="imagen">
                <div class="inner" v-if="!mostrar">
                    <div class="fill">
                        <svg version="1.1" xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink" x="0px" y="0px" width="300px" height="300px" viewBox="0 0 300 300" enable-background="new 0 0 300 300" xml:space="preserve">
                        <path class="waveShape" d="M300,300V2.5c0,0-0.6-0.1-1.1-0.1c0,0-25.5-2.3-40.5-2.4c-15,0-40.6,2.4-40.6,2.4
                        c-12.3,1.1-30.3,1.8-31.9,1.9c-2-0.1-19.7-0.8-32-1.9c0,0-25.8-2.3-40.8-2.4c-15,0-40.8,2.4-40.8,2.4c-12.3,1.1-30.4,1.8-32,1.9
                        c-2-0.1-20-0.8-32.2-1.9c0,0-3.1-0.3-8.1-0.7V300H300z" />
                        </svg>
                    </div>
                </div>
            </div>
        </b-col>
    </b-row>
</div>
</template>

<script>
export default {
    data() {
        return {
            number: 10000,
            tweenedNumber: 10000,
            mostrar: true
        }
    },
    methods: {
        substract: function() {
            setTimeout(() => {
                this.number-=600;
            }, 1000);
        }
    },
    computed: {
        animatedNumber: function() {
            return this.tweenedNumber.toFixed(0);
        }
    },
    watch: {
        number: function(newValue) {
            TweenLite.to(this.$data, 20, {tweenedNumber: newValue});
            setTimeout(()=>this.mostrar=true, 25000)
        }
    }
}
</script>

<style>
    .monosp {
        font-family: 'Roboto Mono', monospace;
        color:rgb(28, 0, 105);
    }
    .fondo {
        background: linear-gradient(left, rgba(255, 255, 255, 0.911), rgba(255,255,255,1)), url('../assets/splash.png');
        background-size: cover;
        background-position: center;
        background-repeat: no-repeat;
    }
    .boton-l {
        background-color: rgb(46, 0, 172);
        border-color: rgba(46,0,172);
    }
    .botella {
        background-image: url('../assets/botella.jpg');
        background-size: contain;
        background-position: center;
        background-repeat: no-repeat;
        background-size: 500px;  
    }
    .botella .imagen {
        height:500px;
    }
    .botella .inner {
        height:280px;
        width: 80px;
        display: inline-block;
        margin-top:128px;
        overflow: hidden;
        border-bottom-left-radius: 70px 20px;
        border-bottom-right-radius: 70px 20px;
        -webkit-backface-visibility: hidden;
        -webkit-transform: translate3d(0,0,0);
    }

    /*Decoradores*/
    .botella .inner:before {
        content: "";
        width: 20px;
        height: 20px;
        background: rgba(255, 255, 255, 0.2);
        border-radius: 50%;
        position: absolute;
        right: 50%;
        top: 80%;
        z-index: 2;
    }
    .botella .inner:after {
        content: "";
        width: 20px;
        height: 40px;
        background: rgba(255, 255, 255, 0.2);
        border-radius: 50%;
        position: absolute;
        right: 25%;
        top: 25%;
        transform: rotate(-20deg);
        z-index: 2;
    }

    .botella .fill {
        -webkit-animation-name: fillAction;
        -webkit-animation-iteration-count: 1;
        -webkit-animation-timing-function: ease-out;
        -webkit-animation-duration: 20s;
        -webkit-animation-fill-mode: forwards;
    }
    .botella .waveShape {
        -webkit-animation-name: waveAction;
        -webkit-animation-iteration-count: 20;
        -webkit-animation-timing-function: linear;
        -webkit-animation-duration: 1s;
        width: 300px;
        height: 150px;
        /*
        fill: #039be4;
        */
        fill:rgba(41, 6, 240, 0.911)
        
        /*
        fill:rgba(28, 138, 211, 0.5)
        */
    }
    @-webkit-keyframes fillAction {
        0% {
            -webkit-transform: translate(0, 300px);
        }
        100% {
            -webkit-transform: translate(0, 10px);
        }
    }
    @-webkit-keyframes waveAction {
        0% {
            -webkit-transform: translate(-150px, 0);
        }
        100% {
            -webkit-transform: translate(0, 0);
        }
    }


.products-background-text {
    position: absolute;
    left: calc(-1 * (100vw - 100%) / 2);
    margin-top: -50px;
    font-weight: 700;
    white-space: nowrap;
    opacity: .05;
    color: #000;
    font-size: 16vw;
    line-height: 1.2;
    text-shadow: 0 0 66px hsla(0,0%,100%,.73);
    z-index: -1;
    font-family: Helvetica Neue,Helvetica,Arial,sans-serif;
}

.caracter-background-text {
    position: absolute;
    right: calc(-1 * (100vw - 100%) / 2);
    margin-top: 500px;
    font-weight: 700;
    white-space: nowrap;
    opacity: .05;
    color: #000;
    font-size: 8vw;
    line-height: 1.2;
    text-shadow: 0 0 66px hsla(0,0%,100%,.73);
    z-index: -1;
    font-family: Helvetica Neue,Helvetica,Arial,sans-serif;
}
    
</style>


