<template>
<div class="main">
    
    <Inicio/>
    <Array/>

    <b-container fluid class="morado">
        <h1>El costo de inversión en una botella inteligente GLU equivale a haber comprado botellas chicas de un solo uso durante un mes.</h1>
    </b-container>

    <Botella/>

    <!--
    <b-container fluid class="naranja">
    </b-container>
    -->

    <Sensores/>
    <VistaTarjetas/>
    <Main/>
</div>
</template>

<script>
import Main from '@/components/Main';
import Sensores from '@/components/Sensores';
import VistaTarjetas from '@/components/VistaTarjetas'
import Array from '@/components/Array'
import Botella from '@/components/Botella'
import Inicio from '@/components/Inicio'

export default {
    components: {
        Sensores,
        Main,
        VistaTarjetas,
        Array,
        Botella,
        Inicio
    }
}
</script>

<style>
    .morado {
        background: linear-gradient(left, rgba(46, 3, 146, 0.911), rgb(58, 40, 221));
        padding: 30px;
        margin-bottom: 70px;
    } 
    .morado h1 {
        color: white;
    }
    .naranja {
        background: linear-gradient(left, rgba(255, 255, 255, 0.952), rgba(255, 255, 255, 0.582)), url('../assets/vector.png');
        background-size: cover;
        background-position: center;
        background-repeat: no-repeat;
        background-position: right;
        height: 300px;
    }
    .main {
       
    }
</style>


